<style lang="scss" scoped="">
  @import "../../../../../style/scss/helpers/functions";

  .third__wrap {
    margin-top: px2rem(80);
    font-size: px2rem(24);
    color: #999999;
    .third__wrap-text {
    }
    .third__wrap-item {
      width: px2rem(644);
      margin: auto;
      margin-top: px2rem(5);
      position: relative;
      img {
        width: 100%;
        display: block;
      }
      .btn-fix-bg {
        background: #000;
        color: #fff;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        bottom: px2rem(18);
        height: px2rem(50);
        line-height: px2rem(50);
        width: px2rem(150);
        text-align: center;
      }
    }
  }

  .window__wrap {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 5;
    display: flex;
    align-items: center;
    justify-content: center;
    .window__inner {
      flex: 1;
      position: relative;
      width: 100%;
    }

    .window__inner {
      background: #404040 url(./bg.png);
      box-shadow: 0 3px 14px 0 rgba(0, 0, 0, 0.50);
      padding: px2rem(45);
    }
    .window__inner-content {
      line-height: px2rem(28);
      font-size: px2rem(20);
      color: #fff;
    }
    .hybtn__wrap {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      margin-top: px2rem(45);
      .hybtn {
        width: px2rem(120);
        height: px2rem(42);
        line-height: px2rem(42);
        background: #000;
        color: #fff;
        text-align: center;
        font-size: px2rem(24);
        &.apply {
          margin-left: px2rem(50);
          background: #51BDEC;
          color: #000;
        }
      }
    }
  }
</style>
<style lang="scss">
  @import "../../../../../style/scss/helpers/functions";

  .window__inner-content {
    p {
      margin-bottom: px2rem(20);
    }
  }
</style>
<template>
  <div class="third__wrap">
    <div class="third__wrap-inner">
      <div class="third__wrap-title">
        <common-title
          :index="`03`"
          :title="`Awards`"
          :bottom="title"
        />
      </div>
      <div class="third__wrap-text">
        <div v-if="innerDontShow!=4" class="third__wrap-item" @click="showWindow('type1')">
          <img src="./1.svg"/>
          <div class="btn-fix-bg"></div>
        </div>
        <div v-if="innerDontShow!=1" class="third__wrap-item" @click="showWindow('type2')">
          <img src="./2.svg"/>
          <div class="btn-fix-bg"></div>
        </div>
        <div v-if="innerDontShow!=2" class="third__wrap-item" @click="showWindow('type3')">
          <img src="./3.svg"/>
          <div class="btn-fix-bg"></div>
        </div>
        <div v-if="innerDontShow!=3" class="third__wrap-item" @click="showWindow('type4')">
          <img src="./4.svg"/>
          <div class="btn-fix-bg"></div>
        </div>
      </div>
    </div>
    <div v-if="show" class="window__wrap">
      <div class="window__inner">
        <div class="window__inner-content" v-html="content"></div>
        <div class="hybtn__wrap">
          <div class="hybtn close" @click="closeWindow"></div>
          <!--<div class="hybtn apply" @click="apply" v-if="url"></div>-->
          <template v-if="type">
            <div class="hybtn apply">
              <div @click="toUrl('type.php?type='+type)"></div>
            </div>
          </template>
          <template v-else>
            <!--<div></div>-->
          </template>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
	import $ from 'jquery'
	import CommonTitle from './../common-title/common-title.vue'

	function GetQueryString(name) {
		var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
		var r = window.location.search.substr(1).match(reg);
		if (r != null) return unescape(r[2]);
		return null;
	}

	const events = ['mousewheel', 'DOMMouseScroll', 'touchmove']
	export default {
		props: {
			refresh: {
				type: Boolean,
				default: false
			},
			title: {
				type: String,
				default: '/  /'
			},
			dontShow: {
				type: String,
				default: GetQueryString('type')
			}
		},
		data() {
			return {
				type: '',
				show: false,
				contentList: {
					'type1': `<p>GTIC AWARDS2017</p>
                    <p><br>
                      -<br>
                      -1<br>
                      -GTIC AWARDS 2018
                    </p>`,
					'type2': `<p>
                    <p><br>
                      -5<br>
                      -2017<br>
                      -GTIC AWARDS 2018
                    </p>
                    <p><br>
                      -2017<br>
                      -30%70%<br>
                      -10
                    </p>`,
					'type3': `<p></p>
                    <p><br>
                      -2017<br>
                      -
                    </p>
                    <p><br>
                      -<br>
                      -30%70%<br>
                      -10
                    </p>`,
					'type4': `<p></p>
                    <p><br>
                      -3<br>
                      -<br>
                      -2017
                    </p>
                    <p><br>
                      -<br>
                      -30%70%<br>
                      -5
                    </p>`,
				},
				urlList: {
					type1: '',
					//GTIC AWARDS 2018AI http://cn.mikecrm.com/Yc0hq7M
					type2: '',//'http://cn.mikecrm.com/Yc0hq7M',
					//GTIC AWARDS 2018 http://cn.mikecrm.com/dKMOXCe
					type3: '',//'http://cn.mikecrm.com/ebP5noq',
					//GTIC AWARDS 2018 http://cn.mikecrm.com/ebP5noq
					type4: '',//'http://cn.mikecrm.com/dKMOXCe',
				},
				typeIdList: {
					type1: '',
					type2: '1',
					type3: '2',
					type4: '3',
				},
				content: '',
				url: '',
				innerDontShow: this.dontShow
			}
		},
		watch: {
			show(newVal) {
				if (newVal) {
					events.forEach((item) => {
						window.addEventListener(item, this._preventDefault, {
							passive: false
						})
					})
				} else {
					events.forEach((item) => {
						window.removeEventListener(item, this._preventDefault)
					})
				}
			}
		},
		components: {
			CommonTitle
		},
		methods: {
			toUrl(url) {
				window.location = url
				this.show = false
				$(window).scrollTop(0)
			},
			showWindow(key) {
				this.content = this.contentList[key]
				if (this.content) {
					this.show = true
					this.url = this.urlList[key]
				}
				this.type = this.typeIdList[key]
				// console.log(this.type)
			},
			closeWindow() {
				this.show = false
			},
			apply() {
				if (this.url) window.location = this.url
			},
			_preventDefault(e) {
				// e.preventDefault()
				// e.stopPropagation()
				// return false
			}
		}
	}
</script>


